.card {
  background-color: transparent;
  height: 220px;
  width: 24%;
  margin: .5%;
  position: relative;
  text-align: left;
}

.card > .img-container {
  overflow: hidden;
  text-align: center;
  
}
.card > .img-container > img {
  width: 100%;
  border-radius: 3%;
}

.zoom {
  -webkit-transition: -webkit-transform .5s;
  transition: -webkit-transform .5s;
  transition: transform .5s;
  transition: transform .5s, -webkit-transform .5s;
}

.zoom:hover {
  -webkit-transform: scale(1.25);
          transform: scale(1.25); 
}







.wrapper {
  padding-top: 2%;
  background-color:rgb(0,0,0);
  background-image:url(http://www.zingerbugimages.com/backgrounds/multi_colored_stars.gif);
  background-position:top left;
  background-repeat:repeat;
  background-attachment:fixed;
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding-left: 7.5%;
  padding-right: 7.5%;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
}

.header {
  width: 100%;
  font-size: 3rem;
  color: white;
  margin-top: .25%;
  margin-bottom: 1.5%;
  padding-left: 2%;
  padding-right: 2%;
}

.title {
  float: left;
  font-weight: bold;
  color: palevioletred;
}

.scores {
  float: right;
}

.digit {
  color: palevioletred;
  /* padding: 0;
  margin: 0; */
}

* {
  box-sizing: border-box;
  font-family: Helvetica;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

